import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'formArray'
})
export class FormArrayPipe implements PipeTransform {


  transform(value, args: string[]): any {
    let keys = [];
    for (let key in value) {
      keys.push({ key: key, value: value[key] });
    }
    return keys;
  }
}



